<template>
    <nav class="navigation">
        <ul>
            <li v-for="nav in navigations"
                :key="nav.label"
                :class="{ active: nav.name == currentPage }"
            >
                <router-link :to="nav.path">
                    {{ nav.label }} 
                    <!--<span v-if="nav.name == currentPage">=> {}</span>-->
                </router-link>
            </li>
        </ul>
    </nav>
</template>

<script>
export default {
  data() {
    return {
      navigations: [
        {
          name: 'showcase',
          label: 'showcase',
          path: '/showcase'
        },
        {
          name: 'resume',
          label: 'resume',
          path: '/resume'
        },
        {
          name: 'contact',
          label: 'contact',
          path: '/contact'
        }
      ]
    }
  },

  computed: {
    currentPage() {
      return this.$route.name;
    }
  }
}
</script>


